:root {
  --recre-red: #cf121b;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: sans-serif;
  color: #333;
}

img,
svg {
  vertical-align: middle;
}

.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 4rem;
  height: calc(100vh - 128px);
  max-width: 1280px;
  margin: 0 auto;
}

.header {
  margin-bottom: 8rem;
}

.logo {
  width: 240px;
}

.sitename {
  margin: 0 auto 4rem;
}

.intro {
  max-width: 48ch;
  font-size: 1rem;
  line-height: 1.77;
}

.links {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.links > *:not(:first-child) {
  margin-left: 24px;
}

.links a {
  display: flex;
  justify-content: center;
  text-decoration: none;
  color: var(--recre-red);
  font-weight: bold;
}

.links a:hover {
  text-decoration: underline;
}

.links a svg {
  margin-right: 4px;
  font-size: 1.5em;
}

.images {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 4rem;
  margin: auto;
  width: 512px;
  height: 512px;
}

.picture {
  width: 512px;
  height: 512px;
  clip-path: path(
    'M247.146 32.174c56.403-9.557 114.145-49.258 165.688-23.765 52.517 25.975 73.871 91.874 89.216 149.84 14.781 55.836 13.003 114.41-4.727 169.324-17.628 54.599-48.89 104.075-94.324 137.459-45.145 33.172-100.573 50.49-155.853 46.369-52.894-3.943-107.261-25.653-140.309-68.445-30.192-39.095-6.352-97.61-23.911-144.2-19.422-51.529-90.052-82.997-82.34-137.71C8.103 107.717 70.187 82.925 116.78 58.571c40.495-21.166 85.571-18.807 130.366-26.397Z  '
  );
}

.blob {
  position: absolute;
  bottom: -48px;
  left: 0;
  z-index: -1;
}

@media (max-width: 1280px) {
  .images {
    position: absolute;
    top: -512px;
    right: -64px;
  }
}

@media (max-width: 1000px) {
  .images {
    position: absolute;
    top: -860px;
    right: -64px;
  }
}

@media (max-width: 768px) {
  .container {
    display: block;
    padding: 12rem 2rem 4rem 2rem;
    align-items: center;
    height: auto;
    overflow: hidden;
  }

  .header {
    margin-bottom: 4rem;
  }

  .images {
    position: absolute;
    top: -320px;
    right: -64px;
    margin: 4rem auto 0;
  }

  .blob {
    position: absolute;
    bottom: -2rem;
    left: 0;
    z-index: -1;
  }
}

@media (max-width: 640px) {
  .container {
    display: block;
    padding: 8rem 2rem 4rem 2rem;
    align-items: center;
    height: auto;
    overflow: hidden;
  }

  .header {
    margin-bottom: 4rem;
  }

  .logo {
    width: 128px;
  }

  .images {
    position: absolute;
    top: -160px;
    right: -64px;
    margin: 4rem auto 0;
    width: 256px;
    height: 256px;
  }

  .picture {
    width: 256px;
    height: 256px;
    clip-path: path(
      'M123.573 16.087c28.202-4.778 57.072-24.629 82.844-11.882 26.258 12.987 36.935 45.937 44.608 74.92 7.39 27.917 6.502 57.204-2.363 84.661-8.815 27.3-24.446 52.038-47.162 68.73-22.573 16.586-50.287 25.245-77.927 23.184-26.447-1.971-53.63-12.826-70.154-34.222-15.096-19.548-3.176-48.805-11.956-72.1-9.71-25.765-45.026-41.498-41.17-68.855 3.758-26.664 34.8-39.06 58.097-51.237 20.248-10.584 42.785-9.404 65.183-13.199Z'
    );
  }

  .blob {
    position: absolute;
    bottom: -2rem;
    left: 0;
    z-index: -1;
    width: 200px;
    height: 180px;
  }
}
